
import Card from 'vux/src/components/card/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

import { mapState } from 'vuex'
import { cloneObj } from '@/utils/utils.js'
import { setTimeout } from 'timers'
export default {
components: {
Card,
Swipeout,
SwipeoutItem,
SwipeoutButton
},
activated() {
this.util.unmask()
this.query()
},
computed: {
...mapState({
userUid: state => state.userUid,
userName: state => state.userName
})
},
created() {},
methods: {
refresh() {
this.page = 1
this.query('refresh')
this.$nextTick(() => {
setTimeout(() => {
this.$refs.scroller.donePulldown()
this.pullupEnabled && this.$refs.scroller.enablePullup()
}, 0)
})
},
isObject(dest, arr) {
var map = {}
for (let i = 0; i < dest.length; i++) {
var ai = dest[i]

if (!map[ai.AddName_CN]) {
arr.push({
AddName_CN: ai.AddName_CN,
data: [ai]
})
map[ai.AddName_CN] = ai
} else {
for (let k = 0; k < arr.length; k++) {
const dj = arr[k]
if (dj.AddName_CN == ai.AddName_CN) {
dj.data.push(ai)
break
}
}
}
}
},
query() {
var _this = this,
url =
'/admin/appAgentAction!runAgent.action?agentId=Agent_Wechat_exercise&appId=WeChat&Type=getStatisticalDetail',
params = {}

this.util.post(url, params, function(res) {
if (res.status) {
var data = []
_this.isObject(res.data.data, data)
for (let i = 0; i < data.length; i++) {
if (data[i].AddName_CN == _this.$route.params.data.AddName_CN) {

_this.arrDel = data[i].data
}
}

} else {
return _this.util.alert(res.msg)
}
})
},
changePullupStatus(enabled) {
this.$refs.scroller.donePullup()
if (enabled) {

this.pullupEnabled = true
} else {
this.$refs.scroller.disablePullup()
this.pullupEnabled = false
}
}
},
data() {
return {
isEnd: false,
arrDel: [],
departId: '',
page: 1,
pageSize: 10
}
}
}



